#democontainer {
    display: flex;
    height: calc(100vh - 75px);
}
.half {
    width: 50%;
    height: 100%;
    padding: 5vw;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.left {
    padding-right: 2.5vw;
}
.right {
    padding-left: 2.5vw;
}
#statuswrap {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}
#instructionswrap {
    display: inline-block;
    align-items: center;
    margin-bottom: 20px;
}

.instructionstitle {
    font-size: 4vw;
    display: inline-block;
    margin: 0;
    padding: 0;
    width: 100%;
}
.instructions {
    font-family: monospace;
    font-size: 1.5vw;
    text-align: justify;
}
.monkey {
    font-family: monospace;
    font-size: 1.75vw;
    text-align: justify;
}
.typed {
    color: var(--black);
    border-right: 3px solid var(--darkblue);
}
.untyped {
    color: gray;
}

#handwrap {
    width:100%;
    height:100%;
    display: block;
}

#handwrap > span {
    font-size: 30px;
}

.leftRightDisplay {
    display: flex;
}
.displayHalf {
    width: 50%;
}
#demo h3 {
    font-size: 2vw;
    margin: 0;
    padding: 0;
}
.displayHalf p {
    padding: 0;
    padding-right: 1em;
    margin-top: 10px;
    font-size: 1.25vw
}